import noData from '@/components/noData'
import back from '@/components/back'

export default {
  data() {
    return {
      button: [
        { flag:false,  evaluate: "全部评价" },
        { flag:false,  evaluate: "好评" },
        { flag:false,  evaluate: "差评" }
      ],
      tabIndex: 0,
      //获取评价数据
      evaluateList: [],
      heartList:[1,2,3,4,5],
      isPatient:true,//当前头像是否是患者头像，用户端-我的评价：显示医生头像医生姓名；医生端-我的评价:显示用户的头像用户姓名
      page: 1,
      haveMore: true,
      DrId:null,
      PatientId:null,
      DrScores:[],
      visible:false,
    }
  },
  components: { 
      noData,
      back
  },
  	created() {
        this.DrId = this.$store.state.userInfo.Data.User.DoctorId;
        console.log(this.$store.state.userInfo)
	    this.PatientId = this.$route.query.PatientId? this.$route.query.PatientId:-1;
	    this.visible = this.$route.query.from? true:false;
	    if(this.DrId && this.PatientId){
	        //从个人中心-订单-查看评价进去的：查看某个用户对某个医生的评价
	        this.isPatient = true;
	    }else if(this.DrId ){
	        //医生主页的评价:查看左右用户对该医生的评价
	        this.isPatient = true;
	    }else if(this.PatientId){
	        //从首页进去的我的评价，显示该用户对所有医生的评价
	        this.isPatient = false;
	    }else{
	          		
	    }
	    this.getList(false)
	},
    mounted(){
        this.getMore()
    },
  methods: {
      evaluateClick(index) {
      		this.button.forEach(v => {
                v.flag = false
            })
          	this.button[index].flag = true
          	this.tabIndex = index
          	//这里的差评/好评是按分数分的，5/4是好评
          	if(this.DrId && this.PatientId){
          		//从用户端个人中心-订单-查看评价进去的
          		this.isPatient = true;
          		if(index == 1){
          			this.DrScores = [5,4];
	          	}else if(index == 2){
	          		this.DrScores = [3,2,1];
	          	}else{
	          		this.DrScores = [];
	          	}
          	}else if(this.DrId){
          		//医生主页的评价
          		this.isPatient = true;
          		if(index == 1){
          			this.DrScores = [5,4];
	          	}else if(index == 2){
	          		this.DrScores = [3,2,1];
	          	}else{
	          		this.DrScores = [];
	          	}
          	}else if(this.PatientId){
          		//从首页进去的我的评价，显示该用户对所有医生的评价
          		this.isPatient = false;
          		if(index == 1){
          			this.DrScores = [5,4];
	          	}else if(index == 2){
	          		this.DrScores = [3,2,1];
	          	}else{
	          		this.DrScores = [];
	          	}
          	}else{
          		
          	};
          	this.getList(false);
      	},
    	getList(flag){
    		//获取患者对某个医生的评价
    		this.$post('/PlatFormAPI/DoctorPatient/QueryDoctorEvaluationByPage',{
    			DrId: this.DrId,
      			PatientId: this.PatientId,
        		DrScores:this.DrScores,
		        PageSize:10,
		        PageIndex:this.page,
      	}).then(res => {
				if (!res.ReturnData || res.ReturnData.length < 10){
                    this.haveMore = false
                }
               	else {
                    this.haveMore = true;
                };
               	if(flag){
               		res.ReturnData.forEach(v => {
                        this.evaluateList.push(v)
                   })
               	}else{
               		this.evaluateList = res.ReturnData;
               	}
			}).catch( err => {
				
			})
// 			this.$get('https://api.douban.com/v2/movie/subject/26004132/reviews',{
// 				apikey:'0b2bdeda43b5688921839c8ecb20399b',
// 				start:0,
// 				count:20,
// 			}).then(res => {
// 				
// 			}).catch( err => {
// 				
// 			})
			
    		/*seekDoctor.evaluateByPainetToDr(DrId,PatientId,DrScores).then(res => {
				this.evaluateList = res.ReturnData
			}).catch( err => {
				console.log(err)
			})*/
    	},
    	getMore(){
    		//加载更多
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.getList(true);
                    }
                }, 100)
            }
        },
        preview(arr,idx){
            const data = {
                position: idx,
                url: arr
            }
            this.$bridge.callhandler('Preview', JSON.stringify(data))
        }
  	},
};



